/* ì˜¤ë¥¸ìª½ í•˜ë‹¨ ì¶”ê°€ ë²„íŠ¼ ìŠ¤íƒ€ì¼ */
.floating-add-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    background-color: #ff5722;
    color: white;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.floating-add-button:hover {
    background-color: #e64a19;
}

