.container {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 20px 20px 20px;
}

.product-section {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /* ì¶”ê°€ */
  gap: 20px;
  margin-bottom: 20px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.image-section {
  flex: 1;
  max-width: 50%; /* ìµœëŒ€ ë„ˆë¹„ë¥¼ ì„¤ì •í•˜ì—¬ í™”ë©´ì´ ì»¤ì ¸ë„ ë¹„ìœ¨ ìœ ì§€ */
  height: 500px;
}

.product-image {
  width: 560px;
  height: 560px;
  border-radius: 8px;
  object-fit: cover; /* ì´ë¯¸ì§€ ë¹„ìœ¨ ìœ ì§€ */
}

.info-section {
  flex: 1;
  width: 580px;
  height: 560px;
  /* ìˆ˜ì • */
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: white;
  font-size: 16px;
  max-width: 580px;
  min-width: 400px;
}


/* ì´ë¯¸ì§€ ìŠ¬ë¼ì´ë” ìŠ¤íƒ€ì¼ */
.image-slider {
  position: relative;
  width: 560px;
  height: 800px;
  overflow: hidden;
}

.prev-btn,
.next-btn {
  position: absolute;
  top: 40%;
  /* ì›ë˜ 50% ì˜€ìŠµë‹ˆë‹¤. */
  transform: translateY(-50%);
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  padding: 10px;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.image-slider:hover .prev-btn,
.image-slider:hover .next-btn {
  opacity: 1;
}

.prev-btn {
  left: 10px;
}

.next-btn {
  right: 10px;
}


.product-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 15px;
}

.product-category {
  font-size: 14px;
  color: gray;
  margin-bottom: 15px;
}

.product-price {
  font-size: 25px;
  margin-top: 25px;
  font-weight: bold;
  margin-bottom: 25px;
  color: black;
}

.product-description {
  line-height: 1.5;
  margin-bottom: 10px;
}

.meta-info {
  margin-bottom: 20px;
  color: gray;
}

/* ë²„íŠ¼ ìŠ¤íƒ€ì¼ */
.button-section {
  display: flex;
  gap: 15px;
}

.like-btn {
  padding: 10px 20px;
  border: 1px solid #ddd;
  background-color: white;
  color: #FF6F0F;
  border-radius: 4px;
  cursor: pointer;
}

.like-btn.liked {
  background-color: #ff6f61;
  color: white;
}

.cta-btn {
  width: 400px;
  padding: 15px 25px;
  background-color: #FF6F0F;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

